@import url(https://fonts.googleapis.com/css2?family=Prompt&display=swap);
body {
	margin: 0;
	font-family: "Prompt", -apple-system, BlinkMacSystemFont, "Segoe UI",
		"Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
		"Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
body,
html {
	height: 100%;
	width: 100%;
}

#root {
	width: 100%;
	height: 100%;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.App {
	width: 100%;
	height: 100%;
	position: relative;
}

.home_headerVideo__4v4E_ {
	width: 100%;
	height: 100%;
	object-fit: cover;
	-webkit-filter: grayscale(1);
	        filter: grayscale(1);
	opacity: 0.1;
	position: absolute;
	z-index: -1;
}

.home_header__3FDEe {
	text-align: center;
	position: absolute;
	top: 50%;
	left: 50%;
	padding: 30px;
	transform: translate(-50%, -50%);
	-webkit-animation: home_fade-in__2K17N 1s;
	        animation: home_fade-in__2K17N 1s;
	white-space: nowrap;
}

.home_header__3FDEe a span[role="img"] {
