	text-decoration: none;
}

.home_nav_bt__38MtC:hover {
	color: black;
}

@media not screen and (max-width: 2000px) {
	.home_title__3SdOf {
		font-size: 7vw;
	}
	.home_description__2vR7W,
	.home_nav_bt__38MtC {
